<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="devScenario"
        label="标签"
        width="100"
        :filters="[{ text: 'Outdoor Site', value: 'Outdoor Site' }, { text: 'Indoor Site', value: 'Indoor Site' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.devScenario === 'Outdoor Site' ? 'primary' : 'success'"
            disable-transitions>{{scope.row.devScenario}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'datatable',
  data() {
    return {
      tableData: [
        {
          devScenario: 'Outdoor Site'
        },
        {
          devScenario: 'Indoor Site'
        },
        {
          devScenario: 'Outdoor Site'
        },
        {
          devScenario: 'Indoor Site'
        }
      ]
    }
  },
  methods: {
    filterTag(value, row) {
      return row.devScenario === value
    }
  }
}
</script>

<style lang="stylus" scoped>

</style>
